function App() {
  return (
    <Auth0Provider domain="kingtechnician.us.auth0.com" clientId="xTbGmROcQrGne2nH7AAEDu1C3E8hKeBW" authorizationParams={{
      redirect_uri:window.location.origin
    }}>
    <Router className="App">
      <Routes className="App-header">
        <Route path = "/" element = {<AuthForm/>}/>
        <Route path="/dashboard" element={<Dashboard/>}/>
        <Route path="/dashboard/about" element={<About/>}/>
        <Route path="/dashboard/search" element={<Search/>}/>
      </Routes>
    </Router>
    </Auth0Provider>
  );
}

function AuthForm()
{
  const {user,isAuthenticated,isLoading} = useAuth0();
  console.log(isAuthenticated);
  let navigate = useNavigate();
  const {loginWithRedirect} = useAuth0();
  if(isAuthenticated)
  {
    navigate("/dashboard")
  }
  return (
    <div className = "App">
      <div className = "App-header">
        <p>This is where the authentication information will go.</p>
        <button onClick ={loginWithRedirect}>Log In</button>
      </div>
    </div>
  )
}

function Search()
{
  const {user,isAuthenticated,isLoading,logout} = useAuth0();
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const navigate = useNavigate();
  const navigateSite = (link) =>{
    navigate(link);
  }
  const [selected, setSelected] = useState(0);
  if(!isAuthenticated)
  {
    return (
      <div className = "App">
        <header className = "homePage App-header">
          <p>Hm, looks like you don't have access.</p>
        </header>
      </div>
    )
  }
  return (
    <div className = "App">
      <header className = "homePage App-header">
        <h3>Search</h3>
        <p>Search for posts here.</p>
        <SpeedDial
          ariaLabel="SpeedDial example"
          sx={{position: 'absolute', width:'fit-content',height:'fit-content', bottom: 0,right:0}}
          icon={<SpeedDialIcon />}
          onClose={handleClose}
          onOpen={handleOpen}
          open={open}
          FabProps = {{
            sx:{
              bgcolor:'#193A6F',
              transition:'all 0.6s'
            }
          }}
        >
          <SpeedDialAction 
          key="Home"
          icon={<HomeIcon />}
          tooltipTitle="Home"
          tooltipOpen
          onClick={()=> {navigate({pathname:'/dashboard'})}}
          />
          <SpeedDialAction
            key="About"
            icon={<InfoIcon />}
            tooltipTitle="About"
            tooltipOpen
            onClick={()=> {navigate({pathname:'/dashboard/about'})}}
            />
          <SpeedDialAction
            key="Search"
            icon={<SearchIcon />}
            tooltipTitle="Search"
            tooltipOpen
            onClick={()=> {navigate({pathname:'/dashboard/search'})}}
            />
            <SpeedDialAction
            key= "New Post"
            icon={<FiberNewIcon />}
            tooltipTitle="New Post"
            tooltipOpen
            onclick={handleClose}
            />
          <SpeedDialAction
          key="Logout"
          icon={<LogoutIcon />}
          tooltipTitle="Logout"
          tooltipOpen
          onClick={() => logout({ logoutParams: { returnTo: window.location.origin } })}
          />
        </SpeedDial>
      </header>
    </div>
  )
}

